    color: whitesmoke;
    padding-top: 18px !important;
} */

.board-row{
    clear: both;/* no element appears to the left or right of the floated element*/
    display: table; /*element to behave like a table*/
    content: "";
}

.nextPlayer{
    margin-bottom: 10px ;
    color: whitesmoke;
    padding-bottom: 10px;
}

/* the individual button that will display X or O */
.square{
    background: black;
    border: 3px solid whitesmoke;
    float: right;
    font-size: 70px;
    font-weight: bold;
    line-height: 25px;
    height: 75px;
    width: 75px;
    margin-right: -1px;
    margin-top: -5px;
    padding: 0 ;
    text-align: center;
    color: whitesmoke;
}

.square:focus{
    outline: invert;
}

.game{
    /* the items to take the same length and be displayed horizontally */
    display: flex;
    flex-direction: row;
    /* align-items: center; */
    justify-content: center;
    /* vertical-align:middle; */
    /* position: relative; */
    /* top: 150%; */
    -webkit-transform: translateY(50%);
            transform: translateY(50%);
}

.game-info{
    margin-left: 100px;
    color: white;
    margin-top: 0;
    width: 150px;
}

.winner-counter{
    display: flex;
    /* flex-direction: row; */
    margin-top: 10px;
    justify-content: space-between;
}


.display-counter{
    font-weight: bold;
    color:orange;
}

/* div .display-counter{
    padding: 0;
} */

div .display-counter span{
    color:rgba(0, 255, 255, 0.6);
}

.reset-counter-button{
    margin-top: 3em;
}

.how-to-play{
    display: flex;
    flex-direction: row;
    justify-content: center;
    margin-top: 1em;
}

.instructions-link{
    color: white;
    padding-bottom: 1em;
    font-size: 14px;
}

.instructions-link a:before{
    color: whitesmoke;
}

.instructions-link a:visited{
    color: orange;
}

#reset-button{
    text-transform: none;
    color: whitesmoke;
}

.footer{
    color: whitesmoke;
    position: fixed;
    bottom: 0 !important;
    width: 100%;
    height: 70px;
    background-color: black;
}

.footer_details{
    margin: 6px 0 0 0;
}

.copyright-icon{
    margin-right: 5px;
}
/* div.display-counter:not(:last-child){
    border-right: 2px solid whitesmoke;
} */
/* mobile styles for responsive view */

/* for medium devices, up to 480px */
@media only screen and (max-width:480px){

    .game{
        display: flex;
        flex-direction: column;
        align-items: center;
        -webkit-transform: translateY(30%);
                transform: translateY(30%);
    }

    .game-info{
        margin-left: 0 !important;
        width: 200px;
        margin-top: 1em;
    }

    .winner-count-header{
        margin-top: 15px;
    }

    .winner-counter{
        margin-top: 1em;
    }
    
    .reset-counter-button{
        margin-top: 2em;
    }
}

/* for smaller devices --up to 412px */
@media only screen and (max-width:412px){

    .game{
        display: flex;
        flex-direction: column;
        align-items: center;
        -webkit-transform: translateY(20%);
                transform: translateY(20%);
    }

    li{
        margin: 0 1px 1px -12% !important;
    } 

    ul li a{
        font-size: 20px !important;
    }
}

#nav-bar{
    transition: top 1s;
}

nav{
    border-bottom: 1px solid white;
}

ul{
    list-style-type: none;
    display: flex;
    flex-direction: row;
    align-content: flex-start;
    margin: auto;
    align-items: center;
}

li{
    padding-bottom:10px;
}

#header-banner{
    text-decoration:none !important;
    font-size: 30px;
    background-image: linear-gradient(to right, orange,white,  red,whitesmoke);
    -webkit-background-clip: text;  
    -moz-background-clip: text;
    background-clip: text;
    color: transparent;
}

@media only screen and (max-width:480px){

    li{
        margin: 1px 1px 1px -10%;
    }

    ul li a{
        text-decoration: none;
        font-size: 25px;
    }
}
main{
    padding-bottom: 70px;
}

h3{
    font-size: 30px;
    padding-top: 40px;
}

.main-content{
    background-color: rgba(0, 0, 0, 0.5);
    max-width: 70%;
    margin: 0 auto !important;
    box-shadow: 0 4px 8px 0 rgba(245, 245, 245, 0.2);
    transition: 0.3s;
    color: whitesmoke;
}

.main-content:hover{
    box-shadow: 0 8px 16px 0 rgba(245, 245, 245, 0.2);
}

.instructions-list{
    list-style-type: none;
    display: block;
    text-align: left;
    padding: 15px 0 0px 15px;
}

.instruction-number{
    background-color:rgba(83, 79, 79, 0.1);
    color: rgba(0, 255, 255, 0.6);
    width: 80px;
    height: 80px;
    /* padding-bottom: 5px; */
    margin-bottom: 5px;
    border-radius: 50%;
    display: flex;
    justify-content: center;
}

.instruction-number span{
    font-size: 60px;
}

.instruction{
    display: flex;
    flex-direction: row;
    padding-bottom: 15px;
}

.instruction p{
    margin-left: 20px;
    font-size: 16px;
    display: flex;
    align-items: center;
}

#x, #o{
    font-size: 25px;
    padding: 2px;
}

#x{
    color: orange;
}

#o{
    color: red;
}

.video-option{
    display: flex;
    flex-basis: 100%;
    align-items: center;
    color: whitesmoke;
    margin: 8px 0;
}

.video-option:before, 
.video-option::after{
    content: "";
    flex-grow: 1;
    background: whitesmoke;
    height: 1px;
    font-size: 0px;
    line-height: 0px;
    margin: 0px 8px;
}

.video-player{
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 30px 0 40px 0;
}

.video-frame{
    border-radius: 5px;
}

/* mobile styles for the page */
@media only screen and (max-width:480px){

    h3{
        font-size: 20px;
    }

    .main-content{
        max-width: 100%;
    }

    .instructions-list{
        display: block;
        padding: 30px 0 0 50px;
    }

    .instruction-number{
        background-color: rgba(83, 79, 79, 0.3);
        margin-bottom: 10px;
        width: 40px;
        height: 40px;
        /* margin-bottom: 10px; */
        border-radius: 50% !important;
        display: flex;
        justify-content: center;
    }

    .instruction-number span{
        font-size: 25px;
        display: flex;
        align-items: center;
    }

    .instruction{
        display: flex;
        flex-direction: row;
        padding-bottom: 30px;
    }

    .instruction p{
        margin-left: 10px;
        font-size: 15px;
        display: flex;
        align-items:flex-start;
        width: moz-fit-content;
        width: -webkit-fit-content;
        width: -moz-fit-content;
        width: fit-content;
        margin-top: -.1em;
    }

    #x, #o{
        font-size: 20px;
        /* padding: 1px; */
    }

    .video-frame{
        width: 95%;
        height: 180px;
    }
    

}
